"use client"

import { useState } from "react"
import {
  Drawer,
  DrawerClose,
  DrawerContent,
  DrawerDescription,
  DrawerFooter,
  DrawerHeader,
  DrawerTitle,
  DrawerTrigger,
} from "@/components/ui/drawer"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Badge } from "@/components/ui/badge"
import { MessageSquare, Bug, HelpCircle, Mail, Send } from "lucide-react"
import { toast } from "sonner"

export default function SupportDrawer() {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    type: "",
    subject: "",
    message: ""
  })
  const [isSubmitting, setIsSubmitting] = useState(false)

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    if (!formData.name || !formData.email || !formData.type || !formData.subject || !formData.message) {
      toast.error("Please fill in all fields")
      return
    }

    setIsSubmitting(true)

    try {
      const webhookPayload = {
        embeds: [
          {
            title: `🎓 Certiswift Support: ${formData.type}`,
            color: 0xea580c, // Orange color
            fields: [
              {
                name: "📝 Subject",
                value: formData.subject,
                inline: false
              },
              {
                name: "👤 Name",
                value: formData.name,
                inline: true
              },
              {
                name: "📧 Email",
                value: formData.email,
                inline: true
              },
              {
                name: "🔍 Type",
                value: formData.type,
                inline: true
              },
              {
                name: "💬 Message",
                value: formData.message,
                inline: false
              }
            ],
            timestamp: new Date().toISOString(),
            footer: {
              text: "Certiswift Support System"
            }
          }
        ]
      }

      const response = await fetch("https://discord.com/api/webhooks/1423290294299918416/nuzMUXM1ChWBFJoCjeIteZPL1Hxm45ILQCwIpZ7OdAG81fUajfBdtuVbVKk7S5OSmwef", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(webhookPayload),
      })

      if (response.ok) {
        toast.success("Support request sent successfully! We'll get back to you soon.")
        setFormData({
          name: "",
          email: "",
          type: "",
          subject: "",
          message: ""
        })
      } else {
        throw new Error("Failed to send message")
      }
    } catch (error) {
      toast.error("Failed to send support request. Please try again.")
      console.error("Error sending support request:", error)
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <Drawer>
      <DrawerTrigger asChild>
        <Button 
          variant="ghost" 
          className="h-10 px-4 py-2 text-sm font-medium transition-colors hover:bg-accent hover:text-accent-foreground"
        >
          <MessageSquare className="w-4 h-4 mr-2" />
          Support
        </Button>
      </DrawerTrigger>
      <DrawerContent>
        <div className="mx-auto w-full max-w-2xl">
          <DrawerHeader>
            <DrawerTitle className="flex items-center gap-2">
              <MessageSquare className="h-5 w-5 text-primary" />
              Contact Support
            </DrawerTitle>
            <DrawerDescription>
              Found a bug or have a question? We're here to help! Fill out the form below and we'll get back to you as soon as possible.
            </DrawerDescription>
          </DrawerHeader>
          
          <form onSubmit={handleSubmit} className="p-4 pb-0 space-y-4">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                  Name *
                </label>
                <Input
                  placeholder="Your full name"
                  value={formData.name}
                  onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                  className="bg-background"
                />
              </div>
              <div className="space-y-2">
                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                  Email *
                </label>
                <Input
                  type="email"
                  placeholder="your.email@example.com"
                  value={formData.email}
                  onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                  className="bg-background"
                />
              </div>
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="space-y-2">
                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                  Type *
                </label>
                <Select value={formData.type} onValueChange={(value) => setFormData({ ...formData, type: value })}>
                  <SelectTrigger className="bg-background">
                    <SelectValue placeholder="Select issue type" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="Bug Report">
                      <div className="flex items-center gap-2">
                        <Bug className="w-4 h-4 text-red-500" />
                        Bug Report
                      </div>
                    </SelectItem>
                    <SelectItem value="Feature Request">
                      <div className="flex items-center gap-2">
                        <HelpCircle className="w-4 h-4 text-blue-500" />
                        Feature Request
                      </div>
                    </SelectItem>
                    <SelectItem value="General Question">
                      <div className="flex items-center gap-2">
                        <MessageSquare className="w-4 h-4 text-green-500" />
                        General Question
                      </div>
                    </SelectItem>
                    <SelectItem value="Course Issue">
                      <div className="flex items-center gap-2">
                        <Mail className="w-4 h-4 text-primary" />
                        Course Issue
                      </div>
                    </SelectItem>
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                  Subject *
                </label>
                <Input
                  placeholder="Brief description of your issue"
                  value={formData.subject}
                  onChange={(e) => setFormData({ ...formData, subject: e.target.value })}
                  className="bg-background"
                />
              </div>
            </div>

            <div className="space-y-2">
              <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                Message *
              </label>
              <Textarea
                placeholder="Please provide detailed information about your issue or question..."
                value={formData.message}
                onChange={(e) => setFormData({ ...formData, message: e.target.value })}
                rows={4}
                className="bg-background"
              />
            </div>

            <div className="flex items-center gap-2 p-3 bg-muted/50 rounded-md">
              <Mail className="w-4 h-4 text-muted-foreground" />
              <p className="text-sm text-muted-foreground">
                Your message will be sent directly to our support team. We typically respond within 24 hours.
              </p>
            </div>
          </form>

          <DrawerFooter>
            <Button 
              type="submit" 
              onClick={handleSubmit}
              disabled={isSubmitting}
              className="w-full"
            >
              {isSubmitting ? (
                <>
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2" />
                  Sending...
                </>
              ) : (
                <>
                  <Send className="w-4 h-4 mr-2" />
                  Send Support Request
                </>
              )}
            </Button>
            <DrawerClose asChild>
              <Button variant="outline">Cancel</Button>
            </DrawerClose>
          </DrawerFooter>
        </div>
      </DrawerContent>
    </Drawer>
  )
}
